@import "../../assets/sass/colors";
@import "../../assets/sass/generalMixins";
.Header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 5rem;

  &__links {
    display: flex;
    gap: 2rem;
    align-items: center;
  }
  &__logo {
    font-size: 36px;
    font-weight: 700;
  }
  &__link {
    text-decoration: none;
    color: $gray;
  }
  &__link:hover {
    color: $veryDarkViolet;
  }
  &__buttons {
    display: flex;
  }
  &__button {
    @include linkPrymary;
  }
}
@media screen and (max-width: 1024px) {
  .Header {
    height: 2rem;
    &__buttons {
      display: none;
    }
    &__logo {
      max-height: 1.5rem;
    }
    &__logo + &__link,
    &__link + &__link {
      display: none;
    }
  }
}
